import React from 'react';
import Image from 'next/image';
import Link from 'next/link';
import { headers } from 'next/headers';

async function getGroup(id: string) {
  const headersList = await headers();
  const host = headersList.get('host');
  const protocol = process.env.NODE_ENV === 'development' ? 'http' : 'https';
  const url = `${protocol}://${host}/api/groups/${id}`;
  const res = await fetch(url, { cache: 'no-store' });
  if (!res.ok) return null;
  const data = await res.json();
  return data.group;
}

const GroupDetailPage = async ({ params }: { params: { id: string } }) => {
  const awaitedParams = await params;
  const group = await getGroup(awaitedParams.id);

  if (!group) {
    return <div className="p-8 text-white">Group not found.</div>;
  }

  return (
    <section className="w-full bg-[#1A1A1A] min-h-screen py-12 sm:py-16 lg:py-20">
      <div className="max-w-screen-xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex items-center gap-6 mb-8">
          {group.photo && (
            <Image src={group.photo} alt={group.name} width={80} height={80} className="rounded-full object-cover border border-gray-700" />
          )}
          <div>
            <h1 className="text-3xl font-bold text-white mb-2">{group.name}</h1>
            <p className="text-gray-300 text-lg">{group.properties.length} Properties</p>
          </div>
        </div>
        <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-6">
          {group.properties.length === 0 ? (
            <div className="text-gray-400 col-span-full">No properties in this group.</div>
          ) : (
            group.properties.map((property: any) => (
              <Link key={property._id} href={`/projects/${property._id}`} className="bg-[#181818] rounded-lg shadow-lg overflow-hidden flex flex-col hover:shadow-xl transition-shadow duration-300">
                <div className="relative w-full h-40 bg-gray-800">
                  {property.gallery && property.gallery[0] && (property.gallery[0].data || property.gallery[0].url) && (
                    <Image
                      src={property.gallery[0].data || property.gallery[0].url}
                      alt={property.gallery[0].name || property.name}
                      fill
                      className="object-cover"
                    />
                  )}
                </div>
                <div className="p-4 flex-1 flex flex-col">
                  <h3 className="text-white text-lg font-medium mb-1">{property.name}</h3>
                  <p className="text-gray-400 text-sm mb-1">{property.location}</p>
                  <span className="text-xs bg-white/10 text-white px-2 py-1 rounded-md w-fit">{property.priceRange && typeof property.priceRange === 'object' ? `${property.priceRange.from.value} ${property.priceRange.from.unit} to ${property.priceRange.to.value} ${property.priceRange.to.unit}` : property.priceRange}</span>
                </div>
              </Link>
            ))
          )}
        </div>
      </div>
    </section>
  );
};

export default GroupDetailPage; 